.roadmap-container {
  margin: 2rem 0;
}

.roadmap-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column; /* Or row if preferred */
  gap: 1rem;
}

.roadmap-step {
  border: 1px solid #ccc;
  padding: 1rem;
  border-radius: 4px;
  opacity: 0.5;
  transition: opacity 0.3s ease;
}

.roadmap-step.completed {
  border-color: #28a745;
  background-color: #e9f7ec;
  opacity: 1;
}

.roadmap-step.current {
  border-color: #007bff;
  background-color: #e7f3ff;
  opacity: 1;
  font-weight: bold;
}

.roadmap-step.unlocked {
  border-color: #ffc107;
  background-color: #fff8e1;
  opacity: 1;
}

.roadmap-step.locked {
  border-color: #dc3545;
  background-color: #fbe9ea;
  opacity: 0.6;
}

.step-number {
  font-weight: bold;
  margin-right: 0.5rem;
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  line-height: 1.5em;
  text-align: center;
  border-radius: 50%;
  background-color: #eee;
  color: #333;
}

.roadmap-step.completed .step-number {
  background-color: #28a745;
  color: white;
}

.roadmap-step.current .step-number {
  background-color: #007bff;
  color: white;
}

.roadmap-step.unlocked .step-number {
  background-color: #ffc107;
  color: #333;
}

.roadmap-step.locked .step-number {
  background-color: #dc3545;
  color: white;
}

.step-title {
  margin-right: 0.5rem;
}

.step-status {
  font-size: 0.9em;
  color: #666;
}

